// ==================== 主题配置统一导出 ====================

// 导出基础类型
export * from './base-types';

// 导出颜色系统
export * from './colors';

// 导出样式系统（阴影和圆角）
export * from './styles';

// 导出排版系统
export * from './typography';

// 导出组件配置
export * from './components';

// 导入类型用于主题配置
import type { BoxShadowConfig, BorderRadiusConfig } from './styles';
import type { ColorConfig } from './colors';
import type { TypographyConfig } from './typography';
import type {
  ToastConfig,
  InputConfig,
  RadioConfig,
  CheckboxConfig,
  SwitchConfig,
  ButtonConfig,
  DropdownConfig,
  TabConfig,
  SliderConfig,
  InputNumberConfig,
  TooltipConfig,
} from './components';

/** 主题模式类型 */
export enum ThemeMode {
  /** 明亮模式 */
  LIGHT = 'light',
  /** 暗黑模式 */
  DARK = 'dark',
}

/** 主题配置 */
export interface CommonThemeConfig {
  /** 主题名称 */
  name: string;
  /** 主题模式（明亮/暗黑） */
  mode: ThemeMode;
  /** 基础颜色 */
  colors: ColorConfig;
  /** 阴影 */
  boxShadow: BoxShadowConfig;
  /** 圆角 */
  borderRadius: BorderRadiusConfig;
  /** 排版 */
  typography: TypographyConfig;
  /** 组件主题配置 */
  components: {
    /** 提示框 */
    toast: ToastConfig;
    /** 输入框 */
    input: InputConfig;
    /** 单选框 */
    radio: RadioConfig;
    /** 复选框 */
    checkbox: CheckboxConfig;
    /** 开关 */
    switch: SwitchConfig;
    /** 按钮 */
    button: ButtonConfig;
    /** 下拉框 */
    dropdown: DropdownConfig;
    /** 标签页 */
    tab: TabConfig;
    /** 滑块 */
    slider: SliderConfig;
    /** 数字输入框 */
    inputNumber: InputNumberConfig;
    /** 提示 */
    tooltip: TooltipConfig;
  };
}

/**
 * 编辑器主题配置
 * 支持通过泛型扩展特定编辑器类型的主题配置
 */
export interface EditorThemeOptions<TExtensions = object> extends CommonThemeConfig {
  /**
   * 扩展主题配置
   * 用于特定编辑器类型的主题配置，如：docx、presentation 等
   */
  extensions?: TExtensions;
}
